<template>
    <div class="all_com">
        <div class="title_com">优惠码管理</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>新增优惠码</span>
            </div>
            <el-form id='form' ref="form" label-position='top' :rules='rules' :model="form" label-width="80px">

                <el-form-item label="优惠活动名称 - (Name)" prop='Name'>
                    <el-input v-model="form.Name"></el-input>
                </el-form-item>
                <el-form-item label="优惠活动英文名称 - (EnglishName)" prop='EnglishName'>
                    <el-input v-model="form.EnglishName"></el-input>
                </el-form-item>
                <el-form-item label="类型 - (Type)" prop='Type'>
                    <el-select  v-model="form.Type" placeholder="请选择">
                        <el-option label="代金券" value="1"></el-option>
                        <el-option label="满减券" value="2"></el-option>
                        <el-option label="比特币优惠券" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="开始时间 - (StartTime)" prop='StartTime'>
                    <el-date-picker value-format='timestamp' v-model="form.StartTime" type="datetime" placeholder="选择日期时间" align="right">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="结束时间 - (EndTime)" prop='EndTime'>
                    <el-date-picker value-format='timestamp' v-model="form.EndTime" type="datetime" placeholder="选择日期时间" align="right">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="达成条件价格 - (ConditionPrice)" prop='ConditionPrice'>
                    <el-input v-model="form.ConditionPrice" type='number'></el-input>
                </el-form-item>
                <el-form-item label="折扣 - (Discount)" prop='Discount'>
                    <el-input v-model="form.Discount" type='number'></el-input>
                </el-form-item>
                <el-form-item label="美元达成条件价格 - (UsdConditionPrice)" prop='UsdConditionPrice'>
                    <el-input v-model="form.UsdConditionPrice" type='number'></el-input>
                </el-form-item>
                <el-form-item label="USD折扣 - (UsdDiscount)" prop='UsdDiscount'>
                    <el-input v-model="form.UsdDiscount" type='number'></el-input>
                </el-form-item>
                <el-form-item label="BTC折扣 - (BtcDiscount)" prop='BtcDiscount'>
                    <el-input v-model="form.BtcDiscount" type='number'></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-if='!editId' @click="onSubmit">提交</el-button>
                    <el-button>取消</el-button>
                </el-form-item>
            </el-form>
        </el-card>

    </div>
</template>
<script>
export default {
    name: "",
    data() {
        return {
            form: {
                
            },
            rules: {
                Name: [
                    { required: false, message: "请输入中文名称", trigger: "blur" }
                ],
                EnglishName: [
                    {
                        required: false,
                        message: "请输入英文名称",
                        trigger: "blur"
                    }
                ],
                Type: [
                    {
                        required: true,
                        message: "请选择类型",
                        trigger: "blur"
                    }
                ],
                StartTime: [
                    {
                        required: true,
                        message: "请选择开始时间",
                        trigger: "blur"
                    }
                ],
                EndTime: [
                    {
                        required: true,
                        message: "请选择结束时间",
                        trigger: "blur"
                    }
                ],
                // ConditionPrice: [
                //     { required: true, message: "请输入达成条件金额", trigger: "blur" }
                // ],
                // Discount: [
                //     {
                //         required: true,
                //         message: "请输入折扣",
                //         trigger: "blur"
                //     }
                // ],
                // UsdConditionPrice: [
                //     { required: true, message: "请输入USD 达成条件金额", trigger: "blur" }
                // ],
                // UsdDiscount: [
                //     {
                //         required: true,
                //         message: "请输入USD 折扣",
                //         trigger: "blur"
                //     }
                // ],
                // BtcDiscount: [
                //     {
                //         required: true,
                //         message: "请输入BTC 折扣",
                //         trigger: "blur"
                //     }
                // ],
                
            }
        };
    },
    methods: {
        onSubmit() {
            this.$refs.form.validate(valid => {
                if(!valid) {
                    return;
                }
                let params = {
                    notice: "创建优惠码成功！"
                };
                Object.assign(params, this.form);
                params.StartTime = params.StartTime / 1000;
                params.EndTime = params.EndTime / 1000;
                this.$api("/admin/coupon/activity_add", params).then(res => {
                    if (res.code == 0) {
                        this.$router.push("/coupon/coupon_list");
                    }
                })
            })
        },
 
    },
    mounted() {
       
    
    }
};
</script>
<style lang='scss' scoped>
@import "../../assets/css/titlecom.scss";
.detaila:hover {
    color: #409eff;
    text-decoration: underline;
}
.search_fun {
    height: 60px;
    position: relative;
    top: 10px;
    float: right;
}
.search_input {
    width: 200px;
}
</style>


